/* eslint-disable no-useless-escape */

export const HtmlUtil = {
  /*1.html*/
  htmlEncode: function(html) {
    //1.DIV
    var temp = document.createElement("div");
    //2.innerText(ie)textContent(google)
    temp.textContent != undefined
      ? (temp.textContent = html)
      : (temp.innerText = html);
    //3.innerHTMLHTML
    var output = temp.innerHTML;
    temp = null;
    return output;
  },
  /*2.html*/
  htmlDecode: function(text) {
    //1.DIV
    var temp = document.createElement("div");
    //2.innerHTML(iegoogle)
    temp.innerHTML = text;
    //3.innerText(ie)textContent(google)HTML
    var output = temp.innerText || temp.textContent;
    temp = null;
    return output;
  },
  /*3.html*/
  htmlEncodeByRegExp: function(str) {
    var s = "";
    if (str.length == 0) return "";
    s = str.replace(/&/g, "&amp;");
    s = s.replace(/</g, "&lt;");
    s = s.replace(/>/g, "&gt;");
    s = s.replace(/ /g, "&nbsp;");
    s = s.replace(/\'/g, "&#39;");
    s = s.replace(/\"/g, "&quot;");
    return s;
  },
  /*4.html*/
  htmlDecodeByRegExp: function(str) {
    var s = "";
    if (str.length == 0) return "";
    s = str.replace(/&amp;/g, "&");
    s = s.replace(/&lt;/g, "<");
    s = s.replace(/&gt;/g, ">");
    s = s.replace(/&nbsp;/g, " ");
    s = s.replace(/&#39;/g, "'");
    s = s.replace(/&quot;/g, '"');
    return s;
  }
};
